.Advertising {
  max-height: 300px;
  object-fit: cover;
  max-width: 1210px;
  width: calc(100% - 24px);
  border-radius: 20px;


  .swiper-container {
    width: 100%;
    max-height: 100%;
    overflow: hidden;
    display: flex;
    flex-direction: column;
  }

  .swiper-slide {
    width: 100%;
    height: 100%;
  }
}